body{
  overflow-x: hidden;
}
.flex{
  display: flex;
  z-index: -1;
}


.click{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  mix-blend-mode:difference;
  pointer-events: none;
}
.clickOn{ 
  transform: scale(8);
  background-color: white;
  pointer-events: none;
}
